const GLOBAL_MAP: Record<string, Record<string | any, any>> = {};

export interface IStore<State> {
  getValue: <K extends keyof State>(key: K) => State[K] | null;
  mergeValue: <K extends keyof State>(key: K, mergeData: State[K]) => void;
}

export const createStore = <State>(namespace: string): IStore<State> => {
  const getValue = <K extends keyof State>(key: K): State[K] | null => {
    return ((GLOBAL_MAP[namespace] || {})[key] as State[K]) || null;
  };

  const mergeValue = <K extends keyof State>(
    key: K,
    mergeData: State[K],
  ) => {
    const data = GLOBAL_MAP[namespace] || {};
    data[key] = { ...data[key], ...mergeData };
    GLOBAL_MAP[namespace] = data;
  };
  return {
    getValue,
    mergeValue,
  };
};
